const $form = document.querySelector('form')
const $input = document.querySelector('form input[type=text]')
const $logs = document.querySelector('#logs')
const $startBtn = document.querySelector('.start')
const $gameRule = document.querySelector('#gameRule')
const $overLay = document.querySelector('.overlay')
const $popUp = document.querySelector('#popUp')
const $greeting = document.querySelector('.greeting')
const $result = document.querySelector('.result')
const $answer = document.querySelector('.answer')
const $exitBtn = document.querySelector('.btn--exit')

// 1. 1 ~ 9 Ïùò Ïà´Ïûê ÏÉùÏÑ±
const numbers = []
for (let n = 0; n < 9; n++) {
  numbers.push(n + 1)
}

// 2. ÏÉÅÎåÄÌé∏Ïù¥ Ï†úÏãúÌïòÎäî ÏàòÎ°ú, 1 ~ 9 Ïùò Ïà´Ïûê Ï§ë ÎûúÎç§Ïùò Ïà´Ïûê ÎΩëÍ∏∞ 
const answer = []
for (let n = 0; n < 4; n++) {
  const index = Math.floor(Math.random() * (numbers.length))
  answer.push(numbers[index])
  numbers.splice(index, 1)
}
console.log(answer)

// 4. Ïú†Ï†ÄÍ∞Ä ÏãúÎèÑÌïú Ïà´ÏûêÏù∏ inputÏùò ÏûÖÎ†•Í∞í Í≤ÄÏÇ¨ 
const tries = []

function checkInputValue(input) {
  if (input.length !== 4) {
    alert('4ÏûêÎ¶¨ Ïà´ÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
    return
  }
  if (input.includes(0)) {
    alert('1 ~ 9 ÍπåÏßÄÏùò Ïà´ÏûêÎßå ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
    return
  }
  if (new Set(input).size !== 4) {
    alert('Ïà´ÏûêÍ∞Ä Ï§ëÎ≥µÎêòÏßÄ ÏïäÍ≤å ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî')
    return
  }
  if (tries.includes(input)) {
    alert('Ïù¥ÎØ∏ ÏãúÎèÑÌïú Í∞íÏûÖÎãàÎã§.')
    return
  }
  return true
}

function fillBillBoard(value, strike, ball, row, out) {
  const $rows = document.createElement('td')
  const $tries = document.createElement('td')
  const $ball = document.createElement('td')
  const $strike = document.createElement('td')
  const $out = document.createElement('td')

  $rows.classList.add('rows')
  $rows.textContent = row

  $tries.classList.add('tries')
  $tries.textContent = value

  $ball.classList.add('ball')
  $ball.textContent = ball

  $strike.classList.add('strike')
  $strike.textContent = strike

  $out.classList.add('out')
  $out.textContent = out

  const $row = document.createElement('tr')
  $row.classList.add(`row`)
  $row.appendChild($rows)
  $row.appendChild($tries)
  $row.appendChild($ball)
  $row.appendChild($strike)
  $row.appendChild($out)

  const $tbody = document.querySelector('tbody')
  $tbody.appendChild($row)
}

function defeated() {
  $popUp.classList.remove('hidden')
  const $emotionLose = document.querySelector('.emotion-lose')
  $emotionLose.classList.remove('hidden')
  $greeting.append(`ÏïÑÏâΩÎÑ§Ïöî`, document.createElement('br'))
  $result.prepend(`Ìå®Î∞∞`, document.createElement('br'))
  $answer.append(`Ï†ïÎãµ: ${answer.join()}`)
  $overLay.classList.remove('hidden')
}

function win() {
  $popUp.classList.remove('hidden')
  const $emotionWin = document.querySelector('.emotion-win')
  $emotionWin.classList.remove('hidden')
  $greeting.append(`Ï∂ïÌïòÌï©ÎãàÎã§`, document.createElement('br'))
  $result.prepend(`ÌôàÎü∞`, document.createElement('br'))
  $answer.append(`Ï†ïÎãµ: ${answer.join()}`)
  $overLay.classList.remove('hidden')
}

function hideGameRule() {
  $gameRule.classList.add('hidden')
  $overLay.classList.add('hidden')
}

// 3. 5. Ïú†Ï†ÄÍ∞Ä Ïà´Ïûê ÎßûÏ∂îÍ∏∞ ÏãúÎèÑ
let out = 0
let row = 1
$form.addEventListener('submit', (event) => {
  event.preventDefault()
  const value = $input.value
  $input.value = ''
  $input.focus()
  if (!checkInputValue(value)) {
    return
  }

  // ÏûÖÎ†•Í∞í Ï°∞Í±¥ Î¨∏Ï†ú ÏóÜÏùÑ Ïãú Ïã§Ìñâ
  if (answer.join('') === value) {
    win()
    return
  }
  if (tries.length >= 9) {
    defeated()
    return
  }

  // Î™á Ïä§Ìä∏ÎùºÏù¥ÌÅ¨ Î™á Î≥ºÏù∏ÏßÄ Í≤ÄÏÇ¨
  let strike = 0
  let ball = 0

  answer.forEach((answerItem, i) => {
    const index = value.indexOf(answerItem)
    if (index > -1) {
      if (index === i) {
        strike += 1
      } else {
        ball += 1
      }
    }
  })


  // ÏïÑÏõÉ Ïùº Îïå
  if (strike === 0 && ball === 0) {
    out++
    fillBillBoard(value, strike, ball, row, out)
  } else {

    fillBillBoard(value, strike, ball, row)
  }
  if (out === 3) {
    defeated()
    return
  }

  // row Í∞Ä 4Í∞ú Ïù¥ÏÉÅÏùº Îïå ÏïûÏÑ† row ÏÑ∏Í∞ú ÏÇ≠Ï†ú
  if (row === 6) {
    const $row1 = document.querySelector('tr:nth-child(2)')
    const $row2 = document.querySelector('tr:nth-child(3)')
    const $row3 = document.querySelector('tr:nth-child(4)')
    const $row4 = document.querySelector('tr:nth-child(5)')
    const $row5 = document.querySelector('tr:nth-child(6)')

    $row1.remove()
    $row2.remove()
    $row3.remove()
    $row4.remove()
    $row5.remove()
  }

  tries.push(value)
  row++
})

$startBtn.addEventListener('click', () => {
  hideGameRule()
  localStorage.setItem('readOrNot', 'read');
})

// gameRule ÏùΩÏùå Ïó¨Î∂Ä ÌôïÏù∏
const savedRead = localStorage.getItem('readOrNot')
if(savedRead !== null) { 
  hideGameRule()
} else {
  $gameRule.classList.remove('hidden')
  $overLay.classList.remove('hidden')
}

$exitBtn.addEventListener('click', () => {
  localStorage.removeItem('readOrNot')
})

$input.focus()